import React, { useEffect, useRef, useState } from "react";
import Wrapper from "../common/wrapper";
import styled from "styled-components";

const PaddingSection = styled.section``;

function SectionType2({ children }) {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef(null);

  useEffect(() => {
    const currentRef = sectionRef.current;
    const observer = new IntersectionObserver(
      ([entry]) => {
        //    isVisible true 
        if (entry.isIntersecting) {
          setIsVisible(true);
        } else {
          setIsVisible(false);
        }
      },
      {
        threshold: 0.1, //  10%    
      }
    );

    if (currentRef) {
      observer.observe(sectionRef.current); // section  
    }

    //     
    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  return (
    <PaddingSection ref={sectionRef}>
      <Wrapper
        className={isVisible ? "visible" : ""}
        $translateY="20px"
        $transition={{ opacity: 0.5, transform: 0.5 }}
      >
        {children}
      </Wrapper>
    </PaddingSection>
  );
}

export default SectionType2;
